@import "../../App.scss";

.sidebar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    color: black;
    .bg {
        z-index: 999;
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        width: 400px;
        background-color: white;
        @include mobile {
            width: 200px;
        }

        .links {
            position: absolute;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 30px;
            @include mobile {
                height: 70%;
                gap: 50px;
            }

            a {
                font-family: $heading-font;
                font-size: 40px;
                color: black;
                @include mobile {
                    font-size: 30px;
                }

                &:hover {
                    color: $primary-color;
                }
            }
        }
    }
    button {
        z-index: 999;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        position: fixed;
        top: 25px;
        left: 25px;
        background-color: transparent;
        border: none;
        cursor: pointer;
    }
}
